.Skills .tabs {
  width: 100%;
}
.Skills .tabs .tabs-navigation button {
  font-size: 1.2em;
}
@media only screen and (max-width: 600px) {
  .Skills .tabs .tabs-navigation .tabs-tab {
    margin: 0 15px;
    padding: 0;
  }
}
.Skills .tabs .tabs-content {
  color: #ffffff;
  font-size: 1.75em;
}
.Skills .tabs .tabs-content .Skills__Container .Skills__Container__Skill {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.Skills .tabs .tabs-content .Skills__Container .Skills__Container__Skill .Skills__Container__Skill__Name-Logo {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 40%;
}
.Skills .tabs .tabs-content .Skills__Container .Skills__Container__Skill .Skills__Container__Skill__Name-Logo .Skills__Container__Skill__Name {
  font-size: 0.7em;
  margin: 0 50px;
}
.Skills .tabs .tabs-content .Skills__Container .Skills__Container__Skill .Skills__Container__Skill__Name-Logo .Skills__Container__Skill__Logo {
  margin-bottom: 15px;
}
.Skills .tabs .tabs-content .Skills__Container .Skills__Container__Skill .Skills__Container__Skill__Name-Logo .Skills__Container__Skill__Logo img {
  width: 40%;
  max-height: 350px;
}
.Skills .tabs .tabs-content .Skills__Container .Skills__Container__Skill .Skills__Container__Skill__Rating {
  width: 60%;
}
@media only screen and (max-width: 600px) {
  .Skills .tabs .tabs-content .Skills__Container .Skills__Container__Skill:last-of-type {
    margin-bottom: 50px;
  }
}
.Skills .Stars {
  color: #fdd43b;
  display: flex;
  flex-wrap: wrap;
  font-size: 1.5em;
  justify-content: center;
  letter-spacing: 15px;
}
@media only screen and (max-width: 600px) {
  .Skills .Stars {
    font-size: 1em;
  }
}
@media screen and (min-width: 601px) and (max-width: 916px) {
  .Skills .Stars {
    font-size: 1.2em;
  }
}
.Skills .Stars .Stars--Avoid-Wrap {
  white-space: nowrap;
}

body {
  margin: 0;
  padding: 0;
  font-family: gill-sans-nova, "Gill Sans", "Gill Sans MT", Calibri, sans-serif;
}

